.WorkPage-section {
  height: 100%;
  background-color: #E99A79;
  color: #efefef;
  display: grid;
  grid-template-rows: 1fr 4fr;
}

.WorkPage-title {
  font-size: 3.5em;
  padding: 0 1em;
  text-align: left;
}

.WorkPage-portfolio {
  display: grid;
  -ms-flex-line-pack: space-evenly;
      align-content: space-evenly;
  text-align: center;
  border: 1px inset #efefef;
  margin: 5%;
  border-radius: 2em;
}


